import Vue from 'vue'
import VueRouter from 'vue-router'

const Login = () => import('../views/login/Login')
const Index = () => import('../views/index/Index')
const Warning = () => import('../views/index/warning/Warning')
const courseManage = () => import('../views/index/courseManage/courseManage')
const Home = () => import('../views/index/home/Home')
const Group = () => import('../views/index/group/Group')

const baseInfo = () => import('../views/index/student/baseInfo')
const perInfo = () => import('../views/index/student/perInfo')
const studyInfo = () => import('../views/index/student/studyInfo')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/login',
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
  },
  {
    path: '/index',
    name: 'Index',
    // redirect: '/index/home',
    component: Index,
    children: [
      { path: 'home', name: 'home', component: Home },
      { path: 'courseManage', name: 'courseManage', component: courseManage },
      { path: 'warning', name: 'Warning', component: Warning },
      { path: 'group', name: 'Group', component: Group },

      { path: 'baseinfo', name: 'baseInfo', component: baseInfo },
      { path: 'perinfo', name: 'perInfo', component: perInfo },
      { path: 'studyinfo', name: 'studyInfo', component: studyInfo },
    ],
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  // base: '/Student-Personas/',
  routes,
})

